/* Container holding the image and the text */
.container-main {
  position: relative;
  margin-bottom: 50px;
  clip: inset(0px, 0px, 200px, 0px);
  /*transition: 0.4s cubic-bezier(1, -1, 0, 2);
  clip-path: circle(600px at center);*/
}

/* Bottom right text */
.text-block-main {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: black;
  opacity: 1;
  color: white;
  background:-webkit-gradient(linear,left top, right top,from(rgba(0, 0, 0, 1)) ,color-stop(20%, rgba(0, 0, 0, 0.5)));
  background:linear-gradient(to right,rgba(0, 0, 0, 1) ,rgba(0, 0, 0, 0.5) 20%);
  background-color:transparent /*this your primary color*/
}


/* Bottom right text */
