:root {
    --main-bg-color: black;
    --secodary-bg-color: #2E2F2F;
    --navbar-bg-color: transparent;
    --accent-bg-color: rgba(245,74,83);
    --touchable-bg-color: rgba(245,74,83);
    --small-margin: 25px;
    --big-margin: 50px;
  }


  .uniform_small_margin {
    margin: 25px;
    margin: var(--small-margin);
  }
  
  .uniform_big_margin {
    margin: 50px;
    margin: var(--big-margin);
  }

  .bottom_big_margin {
    margin-bottom: 50px;
    margin-bottom: var(--big-margin);
  }

  .top_big_margin {
    margin-top: 50px;
    margin-top: var(--big-margin);
  }


  .bottom_small_margin {
    margin-bottom: 25px;
    margin-bottom: var(--small-margin);
  }

  .text_color_accent {
    color: rgba(245,74,83);
    color: var(--accent-bg-color);
  }

  .text_color_accent:hover {
    color: rgba(245,74,83);
    color: var(--accent-bg-color);
  }

  .text_color_main:hover {
    color: black;
    color: var(--main-bg-color);
  }

  .text_decoration {
      text-decoration: underline;
      -webkit-text-decoration-color: rgba(245,74,83);
              text-decoration-color: rgba(245,74,83);
      -webkit-text-decoration-color: var(--accent-bg-color);
              text-decoration-color: var(--accent-bg-color);
  }

  .accent_bottom_border {
    border-bottom: 1px solid rgba(245,74,83);
    border-bottom: 1px solid var(--accent-bg-color);
}

.accent_right_border {
  border-right: 1x solid rgba(245,74,83);
  border-right: 1x solid var(--accent-bg-color);
}

.accent_left_border {
  border-left: 1px solid rgba(245,74,83);
  border-left: 1px solid var(--accent-bg-color);
}

.accent_top_border {
    border-top: 1px solid rgba(245,74,83);
    border-top: 1px solid var(--accent-bg-color);
}


.img_color_accent {
        -webkit-filter: invert(40%) grayscale(100%) brightness(40%) sepia(100%) hue-rotate(-50deg) saturate(400%) contrast(2);
